form {
    .form-row {
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
    }
    .input-label-wrapper {
        display: block; 
        width: 100%; 
        label {
            font-size: 14px; 
            font-weight: 500; 
            line-height: normal; 
            margin-bottom: 8px; 
        }
    }
    input[type="text"], input[type="email"], input[type="url"], input[type="password"], input[type="search"], input[type="tel"], input[type="number"], select {
        display: flex;
        align-items: center;
        background: #FFF;
        border: 1px solid var(--input-border-color);
        border-radius: 6px;
        box-shadow: var(--input-shadow);
        width: 100%;
        height: 36px;
        padding: 11px 16px 11px 16px;
        font-size: 13px;
        font-weight: 500;
        line-height: 13px;
        &::placeholder {
            color: var(--input-placeholder-color);
        }
    }
    &.form {
        .form-row { 
            margin-left: -15px; 
            margin-right: -15px;
            .input-label-wrapper {
                padding: 0 15px;
            }
        }
    }
}